@import "../../assets/style/app";

.roadMap {
  padding-top: 92px;

  @include desktop {
    padding-top: 80px;
  }

  h2.title {
    font-weight: 900;
    font-size: 40px;
    letter-spacing: 0;
    text-align: center;
    color: #fff;
  }

  .sliderWrapper {
    @include onlyMobile();
    margin-top: 80px;
    position: relative;
    overflow: hidden;

    .grid {
      position: absolute;
      z-index: 1;
      width: 990px;
      height: auto;
      left: 50%;
      transform: translateX(-50%);
      top: -50px;
    }

    .ellipse {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      transform: translateX(-50%);
      width: 416px;
      height: 456px;
    }

    .slider {
      position: relative;
      z-index: 2;

      .slide {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }

  .dots {
    margin-top: 80px;
  }

  .cardsDesktop {
    @include onlyDesktop();
    margin-top: 42px;

    .inner {
      @include inner;
      position: relative;
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      grid-column-gap: 24px;
      //grid-template-rows: repeat(2, 1fr);
      //grid-gap: 40px 156px;

      .grid {
        position: absolute;
        z-index: 1;
        width: 990px;
        height: auto;
        top: -14px;
        left: 50%;
        transform: translateX(-50%);
      }

      .ellipse {
        position: absolute;
        z-index: 1;
        top: 100px;
        left: 134px;
        width: 416px;
        height: 456px;
      }

    }
  }

  .card {
    position: relative;
    z-index: 2;
    width: 276px;

    @include desktop {
      width: auto;
    }

    img {
      display: block;
      width: 232px;
      height: auto;

      @include desktop {
        width: 200px;
      }
    }

    .label {
      margin-top: 20px;
      @include Inter;
      font-weight: 800;
      font-size: 20px;
      line-height: 130%;
      letter-spacing: 0;
      color: #fffbfb;
    }

    .text {
      margin-top: 20px;
      @include Inter;
      font-size: 13px;
      line-height: 186%;
      letter-spacing: 0;
      color: #fff;
    }
  }

}