@import '../../assets/style/app';

.firstScreen {
  position: relative;
  height: 612px;
  padding-top: 56px;

  @include desktop {
    height: 594px;
    padding-top: 76px;
  }

  .splineWrapper {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
  }

  .label {
    position: relative;
    z-index: 2;
    @include centerRow;
    border: 1px solid rgba(255, 255, 255, 0.15);
    border-radius: 50px;
    padding: 8px 14px;
    width: 94px;
    height: 42px;
    background: #000;
    margin-left: auto;
    margin-right: auto;

    span {
      font-size: 13px;
      letter-spacing: 0;
      color: #9855ff;
      white-space: nowrap;
    }
  }

  .preTitle {
    position: relative;
    z-index: 2;
    font-size: 30px;
    line-height: 87%;
    letter-spacing: 0;
    text-align: center;
    background: linear-gradient(180deg, #fff 0%, #b372cf 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-top: 36px;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      margin-top: 16px;
      width: 266px;
    }
  }

  h1.title {
    position: relative;
    z-index: 2;
    font-weight: 900;
    font-size: 40px;
    line-height: 100%;
    text-align: center;
    color: #fff;
    margin-top: 48px;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      width: 558px;
      margin-left: auto;
      margin-right: auto;
      margin-top: 24px;
    }
  }

  .startBtn {
    position: relative;
    z-index: 2;
    @include button;
    @include centerRow;
    border-radius: 8px;
    padding: 5px 15px;
    width: 110px;
    height: 36px;
    background: #fff;
    transition: 0.3s;
    margin-top: 48px;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      margin-top: 24px;
    }

    &:hover {
      @include desktop {
        background: rgb(171, 171, 171);
      }
    }

    span {
      font-size: 13px;
      letter-spacing: 0;
      color: #000;
    }
  }


}