@import "../../assets/style/app";

.footer {
  @include container;
  border-top: 1px solid #282729;
  padding-top: 42px;
  padding-bottom: 42px;

  .inner {
    @include inner;

    .top {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .logo {
        display: flex;
        align-items: center;
        text-decoration: none;

        img {
          display: block;
          width: 38px;
          height: 38px;
        }

        p {
          @include onlyDesktop;
          margin-left: 10px;
          font-weight: 900;
          font-size: 17px;
          line-height: 153%;
          letter-spacing: 0em;
          background: linear-gradient(180deg, #fff 0%, #b372cf 100%);
          background-clip: text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }

      .socialLinks {
        display: flex;

        a {
          display: block;
          width: 24px;
          height: 24px;

          &:not(:first-child) {
            margin-left: 18px;

            @include desktop {
              margin-left: 30px;
            }
          }

          svg {

            g {
              transition: 0.3s;
            }
          }

          &:hover {
            @include desktop {
              g {
                opacity: 1;
              }
            }
          }
        }
      }

    }

    .links {
      @include onlyMobile(flex);
      flex-direction: column;
      margin-top: 30px;

      .link {
        text-decoration: none;

        &:not(:first-child) {
          margin-top: 20px;
        }

        p {
          font-size: 13px;
          letter-spacing: 0em;
          color: rgba(255, 255, 255, 0.6);
          transition: 0.3s;
        }

        &:hover {
          p {
            color: #9855ff;
          }
        }
      }

    }

  }
}