@import "../../../assets/style/app";

.card {
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  transition: 0.3s;

  &:hover {
    @include desktop {
      box-shadow: 0 10px 74px 10px rgba(78, 0, 191, 0.41);
      background: linear-gradient(180deg, #010002 0%, #361764 100%);

      .back {
        opacity: 1;
      }
    }
  }

  .back {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    opacity: 0;
    transition: 0.3s;
  }

  .label {
    flex-shrink: 0;
    @include centerRow;
    height: 100px;
    font-weight: 900;
    font-size: 20px;
    letter-spacing: 0;
    color: #fff;
    position: relative;

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 20px;
      bottom: 0;
      right: 20px;
      height: 1px;
      background: #282729;
    }
  }

  .body {
    flex-grow: 1;
    position: relative;
    padding: 4px 20px 78px;

    img {
      display: block;
      width: 150px;
      height: 150px;
    }

    .text {
      margin-top: 24px;
      @include Inter;
      font-size: 13px;
      line-height: 200%;
      letter-spacing: 0;
      color: #fffbfb;
    }

    .joinBtn {
      @include button;
      @include centerRow;
      position: absolute;
      left: 20px;
      bottom: 20px;
      right: 20px;

      border: 1px solid rgba(255, 255, 255, 0.15);
      border-radius: 10px;
      height: 38px;

      backdrop-filter: blur(14px);
      box-shadow: inset 0 0 6px 3px rgba(255, 255, 255, 0.25);
      background: rgba(61, 61, 61, 0.4);
      transition: 0.3s;

      &:hover {
        @include desktop {
          box-shadow: inset 0 0 6px 3px rgba(255, 255, 255, 0.25);
          background: rgba(140, 69, 255, 0.4);
        }
      }

      p {
        font-size: 13px;
        letter-spacing: 0;
        color: #fff;
      }
    }
  }

}