@import "../../assets/style/app";

.integrated {
  padding-top: 24px;
  padding-bottom: 48px;

  @include desktop {
    padding-top: 48px;
    padding-bottom: 96px;
  }

  .top {
    display: flex;
    flex-direction: column;
    align-items: center;

    svg {
      width: 32px;
      height: 32px;
    }

    p {
      margin-top: 24px;
      font-weight: 100;
      font-size: 24px;
      line-height: 108%;
      letter-spacing: 0;
      text-align: center;
      color: rgba(255, 255, 255, 0.7);
      padding-left: 18px;
      padding-right: 18px;

      @include desktop {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  .swiper {
    @include onlyMobile();
    margin-top: 36px;

    .swiperItem {
      @include centerRow;
      border: 1px solid rgba(255, 255, 255, 0.15);
      border-radius: 10px;
      width: 235px;
      height: 98px;

      img {
        display: block;
        width: 155px;
        height: auto;
      }
    }
  }

  .dots {
    margin-top: 24px;
  }

  .itemsDesktop {
    @include onlyDesktop();
    @include container;
    margin-top: 24px;


    .itemsDesktop_inner {
      @include inner;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-column-gap: 10px;

      @include desktop() {
        width: 940px;
      }

      & > div {
        @include centerRow;
        border: 1px solid rgba(255, 255, 255, 0.15);
        border-radius: 10px;
        height: 98px;

        img {
          display: block;
          width: 155px;
          height: auto;
        }

      }

    }
  }

}