@import "../../assets/style/app";

.useCases {
  padding-top: 56px;

  @include desktop {
    padding-top: 96px;
  }

  h2.title {
    font-weight: 900;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: 0;
    text-align: center;
    color: #fff;
    padding-left: 18px;
    padding-right: 18px;

    @include desktop {
      padding-left: 0;
      padding-right: 0;
    }
  }

  .slider {
    @include onlyMobile(flex);
    margin-top: 36px;

    .slide {
      width: 306px;
      height: 488px;
    }
  }

  .dots {
    margin-top: 24px;
  }

  .cardsDesktop {
    @include onlyDesktop();
    @include container;
    margin-top: 80px;

    .cardsDesktop_inner {
      @include inner;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-gap: 32px 10px;

      @include desktop() {
        width: 940px;
      }

    }

  }

}