@import "../../assets/style/app";

.pricing {
  padding-top: 40px;

  @include desktop {
    padding-top: 60px;
  }

  h2.title {
    font-weight: 900;
    font-size: 40px;
    letter-spacing: 0;
    text-align: center;
    color: #fff;
  }

  .cardWrapper {
    padding-top: 30px;
    padding-bottom: 30px;

    @include desktop {
      padding-bottom: 54px;
    }

    .card {
      background-image: url(../../assets/png/pricing_back.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      height: 472px;
      border-top: 1px solid rgba(255, 255, 255, 0.15);
      border-bottom: 1px solid rgba(255, 255, 255, 0.15);
      padding-top: 48px;
      padding-left: 18px;
      padding-right: 18px;

      @include desktop {
        padding-top: 80px;
        padding-left: 0;
        padding-right: 0;
        width: 1100px;
        margin-left: auto;
        margin-right: auto;
        border: 1px solid rgba(255, 255, 255, 0.15);
        border-radius: 10px;
      }

      .card_inner {

        @include desktop {
          width: 620px;
          margin-left: auto;
          margin-right: auto;
        }

        .label {
          font-weight: 900;
          font-size: 50px;
          line-height: 100%;
          letter-spacing: 0;
          text-align: center;
          color: #fff;
        }

        .form {
          border: 1px solid #fff;
          border-radius: 20px;
          background: rgba(0, 0, 0, 0.44);
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-top: 10px;
          padding-bottom: 10px;
          padding-left: 20px;
          padding-right: 8px;
          margin-top: 18px;

          @include desktop {
            margin-top: 60px;
          }

          .field {
            background: none;
            border: none;
            outline: none;
            font-weight: 400;
            font-size: 16px;
            letter-spacing: 0;
            color: #fff;

            &::placeholder {
              font-weight: 400;
              font-size: 16px;
              letter-spacing: 0;
              color: #adadad;
            }
          }

          button {
            @include button;
            @include centerRow;
            border-radius: 11px;
            padding: 10px 24px;
            width: 137px;
            height: 46px;
            background: #fff;

            span {
              font-weight: 400;
              font-size: 16px;
              letter-spacing: 0;
              color: #000;
            }
          }


        }
      }
    }

  }
}