@import "../../assets/style/app";

.usingPrompts {
  @include container;
  overflow: hidden;
  //padding-top: 56px;

  @include desktop {
    //padding-top: 96px;
  }

  .inner {
    @include inner;

    @include desktop {
      display: flex;
    }

    .top {
      border: 1px solid rgba(255, 255, 255, 0.15);
      border-radius: 10px;
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
      background: rgba(0, 0, 0, 0.06);
      padding: 0 20px 12px;

      @include desktop {
        width: 352px;
        flex-shrink: 0;
      }

      img {
        display: block;
        width: 319px;
        height: auto;
        margin-left: auto;
        margin-right: auto;
      }

      .title {
        font-weight: 900;
        font-size: 20px;
        line-height: 130%;
        letter-spacing: 0;
        color: #fff;
        margin-top: 24px;
      }

      .text {
        margin-top: 12px;
        font-weight: 400;
        font-size: 14px;
        line-height: 186%;
        letter-spacing: 0;
        color: rgba(255, 255, 255, 0.25);
      }
    }

    .bottom {
      background-image: url(../../assets/png/usingPromptsBack.png);
      background-size: cover;
      margin-top: 45px;
      height: 536px;
      padding: 324px 24px 0;
      position: relative;
      border-radius: 10px;

      &::before {
        position: absolute;
        z-index: 1;
        content: '';
        display: block;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0,0,0,0.4);
        border-radius: 10px;
      }

      @include desktop {
        flex-grow: 1;
        margin-top: 0;
        margin-left: 10px;
        height: auto;
        padding: 358px 0 0 48px;
      }

      img {
        position: absolute;
        z-index: 2;
        width: 324px;
        height: auto;
        top: -45px;
        right: -157px;

        @include desktop {
          right: -34px;
        }
      }

      .title {
        position: relative;
        z-index: 2;
        font-weight: 900;
        font-size: 20px;
        line-height: 130%;
        letter-spacing: 0;
        color: #fff;
      }

      .text {
        position: relative;
        z-index: 2;
        @include Inter;
        margin-top: 12px;
        font-weight: 400;
        font-size: 14px;
        line-height: 186%;
        letter-spacing: 0;
        color: rgba(255, 255, 255, 0.5);

        @include desktop {
          width: 343px;
        }
      }


    }

  }
}