@import "../../../assets/style/app";

.dots {
  @include onlyMobile(flex);
  justify-content: center;
  margin-top: 24px;

  .dot {
    border-radius: 999px;
    width: 10px;
    height: 10px;
    background: rgba(217, 217, 217, 0.1);
    transition: 0.3s;

    &:not(:first-child) {
      margin-left: 8px;
    }

    &_current {
      width: 20px;
      background: #9855ff;
    }
  }
}