@import "../../assets/style/app";

.header {
  @include container;
  height: 68px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);

  .inner {
    @include inner;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    .logo {
      display: flex;
      align-items: center;
      text-decoration: none;

      img {
        display: block;
        width: 38px;
        height: 38px;
      }

      p {
        @include onlyDesktop;
        margin-left: 5px;
        font-weight: 900;
        font-size: 17px;
        line-height: 153%;
        letter-spacing: 0em;
        background: linear-gradient(180deg, #fff 0%, #b372cf 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }

    .links {
      @include onlyDesktop(flex);
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      border: 1px solid rgba(255, 255, 255, 0.15);
      border-radius: 999px;
      padding: 8px 40px;

      .listItem {
        &:not(:first-child) {
          margin-left: 30px;
        }
      }

      .dropdown {
        @include button;
        display: flex;
        align-items: center;

        p {
          font-size: 13px;
          letter-spacing: 0em;
          color: rgba(255, 255, 255, 0.6);
          transition: 0.3s;
        }

        svg {
          margin-left: 3px;
        }

        &:hover {
          p {
            color: #9855ff;
          }
        }
      }

      .link {
        display: block;
        text-decoration: none;

        p {
          font-size: 13px;
          letter-spacing: 0em;
          color: rgba(255, 255, 255, 0.6);
          transition: 0.3s;
        }

        &:hover {
          p {
            color: #9855ff;
          }
        }
      }


    }


    .right {
      display: flex;
      align-items: center;


      .joinBtn {
        @include button;
        border: 1px solid rgba(255, 255, 255, 0.15);
        padding: 6px;
        border-radius: 12px;

        &_inner {
          @include centerRow;
          width: 109px;
          height: 30px;
          border: 1px solid rgba(255, 255, 255, 0.15);
          border-radius: 8px;
          backdrop-filter: blur(14px);
          box-shadow: inset 0 0 6px 3px rgba(255, 255, 255, 0.25);
          background: rgba(140, 69, 255, 0.4);
          transition: 0.3s;

          &:hover {
            box-shadow: inset 0 0 12px 6px rgba(255, 255, 255, 0.25);
          }

          p {
            font-size: 14px;
            letter-spacing: 0em;
            text-align: center;
            color: #fff;
          }
        }
      }

      .menuBtn {
        @include button;
        @include onlyMobile();
        width: 32px;
        height: 32px;
        margin-left: 10px;
      }
    }

  }


}